import { Card, Form, Input, Button, notification } from "antd";
import { LoginOutlined } from "@ant-design/icons";
import http from "../../services/http";

const ToolIndexMenowAuthForm = (props) => {
  const [form] = Form.useForm();
  const onFinish = (data) => {
    props.setSpinning(true);
    http
      .post("/indexmenow/login", data)
      .then(() => {
        notification.success({
          message: "Started session successfully.",
        });
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      })
      .catch((err) => {
        notification.error({
          message: err.response.data,
        });
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      });
  };

  return (
    <Card>
        <Form
            name='toolindex_auth_form'
            layout='vertical'
            onFinish={onFinish}
            autoComplete="off"
            form={form}
        >
            <Form.Item
                label='E-Mail'
                name='email'
                rules={[{ required: true, message: 'Please put down your email.' }]}
            >
                <Input type='email'/>
            </Form.Item>
            <Form.Item
                label='Password'
                name='password'
                rules={[{ required: true, message: 'Please put down your password.' }]}
            >
                <Input type="password"/>
            </Form.Item>
            <Form.Item style={{ float: 'right' }}>
                <Button type="primary" htmlType="submit">
                    <LoginOutlined/> IndexMenow Login
                </Button>
            </Form.Item>
        </Form>
    </Card>
  )
};
export default ToolIndexMenowAuthForm