import { useState, useEffect } from 'react';
import { Card, Button, Table, Modal, Form, Input, notification, Popconfirm } from 'antd';
import { PlusOutlined, EditOutlined, DeleteOutlined } from '@ant-design/icons';
import http from '../services/http';

const WordpressSites = () => {
    const [createForm] = Form.useForm();           
    const [updateForm] = Form.useForm();           
    const [isOpenNewModal, setIsOpenNewModal] = useState(false);
    const [dataSource, setDataSource] = useState([]);
    const [isOpenEditModal, setIsOpenEditModal] = useState(false);
    const [siteId, setSiteId] = useState(0);
    const columns = [{
        title: 'URL',
        dataIndex: 'url',
    }, {
        title: 'Membership API Key',
        dataIndex: 'membershipApiKey'
    },
    {
        title: 'Actions',
        dataIndex: 'action',
        width: 100,
        render: (_, record) => {
            return (
                <Button.Group>
                    <Button type="primary" ghost onClick={() => handleEdit(record._id)}><EditOutlined/></Button>
                    <Popconfirm
                        title="Are you sure to delete?"
                        onConfirm={() => handleDelete(record._id)}
                    >
                        <Button type="primary" danger ghost><DeleteOutlined/></Button>
                    </Popconfirm>
                </Button.Group>
            );
        }
    },
];
    const handleNewOpen = () => {
        createForm.resetFields();
        setIsOpenNewModal(true);
    }
    const handleNewAdd = () => {
        createForm.submit();
    }
    const handleNewSave = (data) => {
        http.post("/sites", data)
        .then(() => {
            notification.success({
                message: "Created successfully."
            });
            setIsOpenNewModal(false);
        })
        .catch((err) => {
            notification.error({
                message: err.response.data
            });
        })
    }
    const handleNewCancel = () => {
        setIsOpenNewModal(false);
    }
    const handleEdit = (id) => {
        http.get(`/sites/${id}`).then(({data}) => {
            setSiteId(id);
            updateForm.setFieldsValue(data);
            setIsOpenEditModal(true);
        });
    }
    const handleEditCancel = () => {
        updateForm.resetFields();
        setIsOpenEditModal(false);
    }
    const handleUpdate = () => {
        updateForm.submit();
    }
    const handleUpdateSubmit = (data) => {
        http.put(`/sites/${siteId}`, data)
        .then(({data}) => {
            notification.success({
                message: "Updated successfully."
            });
            setIsOpenEditModal(false);
        }).catch(err => {
            notification.error({
                message: err.toString()
            });
        });
    }
    const handleDelete = (id) => {
        http.delete(`/sites/${id}`)
        .then(({data}) => {
            fetchSites();
            notification.success({
                message: "Deleted successfully."
            });
        }).catch(err => {
            notification.error({
                message: err.toString()
            });
        });
    }
    const fetchSites = () => {
        http.get('/sites').then(({data}) => {
            let sites = data.map((site, idx) => {
                site.key = idx;
                return site;
            });
            setDataSource(sites);
        });
    }
    useEffect(() => {
        fetchSites();
    }, [isOpenNewModal, isOpenEditModal]);
    return (
        <Card
            className="wordpress-sites"
            title="Wordpress Sites"
            extra={<Button type="primary" onClick={handleNewOpen}><PlusOutlined/> Add New</Button>}
            style={{ marginBottom: 15 }}
        >
            <div style={{overflowX: 'auto'}}>
                <Table 
                    columns={columns} 
                    dataSource={dataSource}
                />
            </div>
            <Modal 
                title="Add New Site" 
                open={isOpenNewModal}
                onOk={handleNewAdd} 
                onCancel={handleNewCancel}
            >
                <Form
                    name="new_site_modal"
                    layout="vertical"
                    autoComplete="off"
                    onFinish={handleNewSave}
                    form={createForm}
                >
                    <Form.Item
                        label="URL"
                        name="url"
                        rules={[{ required: true, message: 'Please put down the URL of a web site.' }]}
                    >
                        <Input placeholder="Wordpress URL"/>
                    </Form.Item>
                    <Form.Item
                        label="API Key"
                        name="membershipApiKey"
                        rules={[{ required: true, mesage: 'Please put down the membership key of a web site.' }]}
                    >
                        <Input placeholder="Membership API Key"/>
                    </Form.Item>
                </Form>
            </Modal>
            <Modal 
                title="Edit Site" 
                open={isOpenEditModal}
                onOk={handleUpdate} 
                onCancel={handleEditCancel}
            >
                <Form
                    name="edit_site_modal"
                    layout="vertical"
                    autoComplete="off"
                    onFinish={handleUpdateSubmit}
                    form={updateForm}
                >
                    <Form.Item
                        label="URL"
                        name="url"
                        rules={[{ required: true, message: 'Please put down the URL of a web site.' }]}
                    >
                        <Input placeholder="Wordpress URL"/>
                    </Form.Item>
                    <Form.Item
                        label="API Key"
                        name="membershipApiKey"
                        rules={[{ required: true, mesage: 'Please put down the membership key of a web site.' }]}
                    >
                        <Input placeholder="Membership API Key" />
                    </Form.Item>
                </Form>
            </Modal>
        </Card>
    )
}
export default WordpressSites;