import { useState, useEffect } from "react";
import {
  Card,
  Button,
  Table,
  Modal,
  Form,
  Select,
  Input,
  notification,
  Popconfirm,
  Switch,
} from "antd";
import {
  PlusOutlined,
  EditOutlined,
  DeleteOutlined,
  SnippetsOutlined,
  CopyOutlined,
  CloseOutlined,
} from "@ant-design/icons";
import http from "../services/http";
const proxies = [
  { key: "semrush", domain: "www.semrush.com" },
  { key: "static.semrush", domain: "static.semrush.com" },
  { key: "spyfu", domain: "www.spyfu.com" },
  { key: "seolyze", domain: "www.seolyze.com" },
  { key: "sistrix", domain: "www.sistrix.com" },
  { key: "linkcentaur", domain: "www.linkcentaur.com" },
  { key: "spamzilla", domain: "www.spamzilla.io" },
  { key: "seodity", domain: "app.seodity.com" },
  { key: "rytrme", domain: "rytr.me" },
  { key: "wordai", domain: "wai.wordai.me" },
  { key: "keywordrevealer", domain: "keywordrevealer.com" },
  { key: "nichescraper", domain: "nichescraper.com" },
  { key: "pipiads", domain: "pipiads.com" },
  { key: "keywordkeg", domain: "app.keywordkeg.com" },
  { key: "paraphraser", domain: "www.paraphraser.io" },
  { key: "buzzsumo", domain: "buzzsumo.com" },
  { key: "articleforge", domain: "af.articleforge.com" },
  { key: "bigspy", domain: "bigspy.com" },
  { key: "colinkri", domain: "www.colinkri.com" },
  { key: "dinorank", domain: "dinorank.com" },
  { key: "yourtext", domain: "yourtext.guru" },
  { key: "babbar", domain: "www.babbar.tech" },
  { key: "firstfr", domain: "1.fr" },
  { key: "textoptimizer", domain: "textoptimizer.com" },
  { key: "onehourindexing", domain: "onehourindexing.co" },
  { key: "ranxplorer", domain: "ranxplorer.com" },
  { key: "majestic", domain: "majestic.com" },
  { key: "woorank", domain: "www.woorank.com" },
  { key: "seobserver", domain: "app.seobserver.com" },
  { key: "seozoom", domain: "www.seozoom.it" },
  { key: "affilisting", domain: "affilisting.com" },
  { key: "explodingtopics", domain: "explodingtopics.com" },
  { key: "copyscape", domain: "www.copyscape.com" },
  { key: "keywordcupid", domain: "keywordcupid.com" },
  { key: "serpstat", domain: "serpstat.com" },
  { key: "plagium", domain: "premium.plagium.com" },
  { key: "closerscopy", domain: "www.closerscopy.com" },
  { key: "linkody", domain: "www.linkody.com" },
  { key: "alishark", domain: "alishark.com" },
  { key: "pexda", domain: "pexda.com" },
  { key: "zonbase", domain: "www.zonbase.com" },
  { key: "analyzonbase", domain: "analytics.zonbase.com" },
  { key: "dropship", domain: "app.dropship.io" },
  { key: "espinner", domain: "espinner.net" },
  { key: "asinseed", domain: "www.asinseed.com" },
  { key: "sellersprite", domain: "www.sellersprite.com" },
  { key: "kindleranker", domain: "www.kindleranker.com" },
  { key: "iconscout", domain: "iconscout.com" },
  { key: "spinrewriter", domain: "www.spinrewriter.com" },
  { key: "pacdora", domain: "www.pacdora.com" },
  { key: "accounttemplatemonster", domain: "account.templatemonster.com" },
  { key: "templatemonster", domain: "www.templatemonster.com" },
  { key: "toolindexmenow", domain: "tool.indexmenow.com" },
  { key: "publicwww", domain: "publicwww.com" },
  { key: "searchatlas", domain: "dashboard.searchatlas.com" },
  { key: "wincher", domain: "app.wincher.com" },
];
const ProxyServers = (props) => {
  const [createForm] = Form.useForm();
  const [updateForm] = Form.useForm();
  const [isOpenNewModal, setIsOpenNewModal] = useState(false);
  const [dataSource, setDataSource] = useState([]);
  const [isOpenEditModal, setIsOpenEditModal] = useState(false);
  const [proxyId, setProxyId] = useState(0);
  const [isOpenSinppetModal, setIsOpenSnippetModal] = useState(false);
  const [snippetForAdmin, setSnippetForAdmin] = useState(true);
  const columns = [
    {
      title: "Domain Name",
      dataIndex: "domain",
    },
    {
      title: "Type",
      dataIndex: "type",
    },
    {
      title: "Actions",
      dataIndex: "action",
      width: 140,
      render: (_, record) => {
        return (
          <div>
            <Button type="primary" ghost onClick={() => handleEdit(record._id)}>
              <EditOutlined />
            </Button>
            <Button onClick={() => handleSinppet(record)}>
              <SnippetsOutlined />
            </Button>
            <Popconfirm
              title="Are you sure to delete?"
              onConfirm={() => handleDelete(record._id)}
            >
              <Button type="primary" danger ghost>
                <DeleteOutlined />
              </Button>
            </Popconfirm>
          </div>
        );
      },
    },
  ];
  const getMainDomain = (domain) => {
    let segments = domain.split(".");
    let mainDomain = "";
    for (let i = 0; i < segments.length; i++) {
      if (i > 0) mainDomain += "." + segments[i];
    }
    return mainDomain;
  };
  const handleNewOpen = () => {
    createForm.resetFields();
    setIsOpenNewModal(true);
  };
  const newChooseType = (val) => {
    createForm.setFieldValue("type", val);
  };
  const handleNewAdd = () => {
    createForm.submit();
  };
  const handleNewSave = (data) => {
    props.setSpinning(true);
    http
      .post("/proxies", data)
      .then(() => {
        notification.success({
          message: "Created successfully.",
        });
        setIsOpenNewModal(false);
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      })
      .catch((err) => {
        notification.error({
          message: err.response.data,
        });
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      });
  };
  const handleNewCancel = () => {
    setIsOpenNewModal(false);
  };
  const handleEdit = (id) => {
    props.setSpinning(true);
    http
      .get(`/proxies/${id}`)
      .then(({ data }) => {
        setProxyId(id);
        updateForm.setFieldsValue(data);
        setIsOpenEditModal(true);
        props.setSpinning(false);
      })
      .catch((err) => {
        notification.error({
          message: err.response.data,
        });
        props.setSpinning(false);
      });
  };
  const handleEditCancel = () => {
    updateForm.resetFields();
    setIsOpenEditModal(false);
  };
  const handleUpdate = () => {
    updateForm.submit();
  };
  const handleUpdateSubmit = (data) => {
    props.setSpinning(true);
    http
      .put(`/proxies/${proxyId}`, data)
      .then(({ data }) => {
        notification.success({
          message: "Updated successfully.",
        });
        setIsOpenEditModal(false);
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      })
      .catch((err) => {
        notification.error({
          message: err.response.data,
        });
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      });
  };
  const handleDelete = (id) => {
    props.setSpinning(true);
    http
      .delete(`/proxies/${id}`)
      .then(({ data }) => {
        fetchSites();
        notification.success({
          message: "Deleted successfully.",
        });
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      })
      .catch((err) => {
        notification.error({
          message: err.toString(),
        });
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      });
  };
  const fetchSites = () => {
    props.setSpinning(true);
    http
      .get("/proxies")
      .then(({ data }) => {
        let proxies = data.map((proxy, idx) => {
          proxy.key = idx;
          return proxy;
        });
        setDataSource(proxies);
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      })
      .catch((err) => {
        notification.error({
          message: err.toString(),
        });
        setTimeout(() => {
          props.setSpinning(false);
        }, 500);
      });
  };
  const handleSinppet = (proxy) => {
    setProxyId(proxy._id);
    setIsOpenSnippetModal(true);
  };
  const handleSnippetCopy = () => {
    let code = document.querySelector(".code-snippet-content").innerText;
    navigator.clipboard.writeText(code);
    notification.success({
      message: "Copied successfully.",
    });
  };
  const handleSinppetCancel = () => {
    setIsOpenSnippetModal(false);
  };
  useEffect(() => {
    fetchSites();
  }, [isOpenNewModal, isOpenEditModal]);
  return (
    <Card
      className="proxy-server-domains"
      title="Proxy Server Domains"
      extra={[
        <Button key="1" type="primary" onClick={handleNewOpen}>
          <PlusOutlined /> Add
        </Button>,
        <Button
          key="2"
          onClick={() => {
            handleSinppet({ _id: 0 });
          }}
        >
          <SnippetsOutlined /> Spinnet
        </Button>,
      ]}
      style={{ marginBottom: 15 }}
    >
      <div style={{ overflowX: "auto" }}>
        <Table columns={columns} dataSource={dataSource} />
      </div>
      <Modal
        title="Add New Domain"
        open={isOpenNewModal}
        onOk={handleNewAdd}
        onCancel={handleNewCancel}
      >
        <Form
          name="new_domain_modal"
          layout="vertical"
          autoComplete="off"
          onFinish={handleNewSave}
          form={createForm}
          initialValues={{
            type: "semrush",
          }}
        >
          <Form.Item
            label="Domain Name"
            name="domain"
            rules={[
              {
                required: true,
                message: "Please put down the domain of a web site.",
              },
              {
                pattern:
                  /^([a-zA-Z0-9][a-zA-Z0-9-_]*\.)*[a-zA-Z0-9]*[a-zA-Z0-9-_]*[[a-zA-Z0-9]+$/,
                message: "The pattern of domain name is incorret.",
              },
            ]}
          >
            <Input addonBefore="https://" placeholder="Domain Name for Proxy" />
          </Form.Item>
          <Form.Item
            label="Proxy Type"
            name="type"
            rules={[
              {
                required: true,
                mesage: "Please choose the type of a web site.",
              },
            ]}
          >
            <Select
              placeholder="Please select a proxy type."
              onChange={newChooseType}
            >
              {proxies.map((proxy, idx) => (
                <Select.Option key={idx} value={proxy.key}>
                  {proxy.domain}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
        </Form>
      </Modal>
      <Modal
        title="Edit Domain"
        open={isOpenEditModal}
        onOk={handleUpdate}
        onCancel={handleEditCancel}
      >
        <Form
          name="edit_domain_modal"
          layout="vertical"
          autoComplete="off"
          onFinish={handleUpdateSubmit}
          form={updateForm}
        >
          <Form.Item
            label="Domain Name"
            name="domain"
            rules={[
              {
                required: true,
                message: "Please put down the domain of a web site.",
              },
              {
                pattern:
                  /^([a-zA-Z0-9][a-zA-Z0-9-_]*\.)*[a-zA-Z0-9]*[a-zA-Z0-9-_]*[[a-zA-Z0-9]+$/,
                message: "The pattern of domain name is incorret.",
              },
            ]}
          >
            <Input placeholder="Domain Name for Proxy" />
          </Form.Item>
          <Form.Item
            label="Proxy Type"
            name="type"
            rules={[
              {
                required: true,
                mesage: "Please choose the type of a web site.",
              },
            ]}
          >
            <Select
              placeholder="Please select a proxy type."
              onChange={newChooseType}
            >
              {proxies.map((proxy, idx) => (
                <Select.Option key={idx} value={proxy.key}>
                  {proxy.domain}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
        </Form>
      </Modal>
      <Modal
        title="Code Snippet"
        open={isOpenSinppetModal}
        onCancel={handleSinppetCancel}
        width={900}
        footer={[
          <Switch
            defaultChecked
            checkedChildren="A"
            unCheckedChildren="M"
            onChange={setSnippetForAdmin}
            style={{ marginRight: 10 }}
          />,
          <Button key="copy" onClick={handleSnippetCopy}>
            <CopyOutlined /> Copy
          </Button>,
          <Button key="close" onClick={handleSinppetCancel}>
            <CloseOutlined /> Close
          </Button>,
        ]}
      >
        <code className="code-sinppet">
          <div className="code-snippet-content">
            <div>&lt;?php</div>
            <div className="ml-20">
              {snippetForAdmin ? (
                <div>{`if (current_user_can('administrator')) {`}</div>
              ) : (
                <div></div>
              )}
              <div className="ml-20">
                $key = "5KcJd3Ydf6pDG210OMyGKAv3rXHOPRwSyD1hg9E6";
              </div>
              <div className="ml-20">$nonce = wp_create_nonce("wp_rest");</div>
              <div className="ml-20">
                $cookies = LOGGED_IN_COOKIE . "=" .
                (isset($_COOKIE[LOGGED_IN_COOKIE]) ?
                htmlspecialchars($_COOKIE[LOGGED_IN_COOKIE]) : "");
              </div>
              <div className="ml-20">
                $useragent = $_SERVER["HTTP_USER_AGENT"];
              </div>
              <div className="ml-20">$time = time();</div>
              {snippetForAdmin ? (
                <div className="ml-20">
                  $data = json_encode([get_current_user_id(), $cookies, $nonce,
                  1]);
                </div>
              ) : (
                <div className="ml-20">
                  $data = json_encode([get_current_user_id(), $cookies, $nonce,
                  0]);
                </div>
              )}
              <div className="ml-20">
                $signature = base64_encode(hash_hmac("sha1",
                $useragent . "\n" . $time . $data, $key, false));
              </div>
              <div className="ml-20">
                $str =
                $signature . "#" . base64_encode($time) . "#" . base64_encode($data);
              </div>
              <div className="ml-20">
                $site = $_SERVER["REQUEST_SCHEME"] . "://" .
                $_SERVER["HTTP_HOST"];
              </div>
              <div className="ml-20">
                {dataSource.length && snippetForAdmin ? (
                  <div>
                    {`echo "<form method='POST' action='https://admin${getMainDomain(
                      dataSource[0].domain
                    )}/authorize' target='_blank'>";`}
                    <div className="ml-20">{`echo "<input type='hidden' name='token' value='" .$str. "'>";`}</div>
                    <div className="ml-20">{`echo "<input type='hidden' name='site' value='" . $site . "'>";`}</div>
                    <div className="ml-20">{`echo "<input type='submit' value='admin for seoserver'>";`}</div>
                    {`echo "</form>";`}
                  </div>
                ) : (
                  <div></div>
                )}
                {dataSource.map((proxy, idx) => {
                  if (proxyId === 0) {
                    return (
                      <div key={idx}>
                        {`echo "<form method='POST' action='https://${proxy.domain}/authorize' target='_blank'>";`}
                        <div className="ml-20">{`echo "<input type='hidden' name='token' value='" .$str. "'>";`}</div>
                        <div className="ml-20">{`echo "<input type='hidden' name='site' value='" . $site . "'>";`}</div>
                        <div className="ml-20">{`echo "<input type='submit' value='${proxy.type} for seoserver'>";`}</div>
                        {`echo "</form>";`}
                      </div>
                    );
                  } else if (proxy._id === proxyId) {
                    return (
                      <div key={idx}>
                        {`echo "<form method='POST' action='https://${proxy.domain}/authorize' target='_blank'>";`}
                        <div className="ml-20">{`echo "<input type='hidden' name='token' value='" .$str. "'>";`}</div>
                        <div className="ml-20">{`echo "<input type='hidden' name='site' value='" . $site . "'>";`}</div>
                        <div className="ml-20">{`echo "<input type='submit' value='${proxy.type} for seoserver'>";`}</div>
                        {`echo "</form>";`}
                      </div>
                    );
                  } else {
                    return false;
                  }
                })}
              </div>
              {snippetForAdmin ? <div>{`}`}</div> : <div></div>}
            </div>
            <div>?&gt;</div>
          </div>
        </code>
      </Modal>
    </Card>
  );
};
export default ProxyServers;
