import React, { useState, useEffect } from 'react';
import { Card, Table, Button } from 'antd';
import { FileProtectOutlined, EyeOutlined } from '@ant-design/icons';
import http from '../services/http';

const ServerLogs = () => {
    const [dataSource, setDataSource] = useState([]);
    const columns = [{
        title: 'Name',
        dataIndex: 'name',
        render: (_, record) => {
            return (
                <React.Fragment>
                    <FileProtectOutlined/> {record.name}
                </React.Fragment>
            )
        }
    }, {
        title: 'Actions',
        dataIndex: 'action',
        width: 50,
        render: (_, record) => {
            return (
                <Button type="primary" ghost onClick={() => handleOpen(record.name)}><EyeOutlined /></Button>
            )
        }
    }];
    const handleOpen = (log) => {
        window.open(`/logs/${log}`);
    } 
    useEffect(() => {
        http.get('/logs').then(({data}) => {
            let logs = data.map((log, idx) => {
                log.key = idx;
                return log;
            });
            setDataSource(logs);
        });
    }, []);
    return (
        <Card
            className="server-logs"
            title="Server Logs"
            style={{ marginBottom: 15 }}
        >
            <Table columns={columns} dataSource={dataSource}/>
        </Card>
    );
}

export default ServerLogs;